.movieCard{
    width: 1400px;
    margin: 0 auto;
    padding: 40px 0 0 20px;

}
.movieCard .posters{
    display: flex;
    gap: 20px;
}
.movieCard h2{
    margin-bottom: 20px;
}
.movieCard .posters img{
    width: 150px;
    height: 225px;
    border-radius: 8px;
    box-shadow: 10px 10px 10px #b1b1b1;
}
.movieCard .posters>.card>.title{
    margin-top: 15px;
}
.movieCard .posters>.card>.title>h3{
    margin-bottom: 5px;
}
.movieCard .posters>.card>.title>p{
    font-size: 12px;
}