.banner{
    width: 1400px;
    height: 400px;
    margin: 0 auto;
    object-fit: contain;
}
.banner .contents{
    padding: 90px 40px;
}
.banner .contents .title h1{
    font-size: 45px;
    font-weight: bold;
    color: #fff;
}
.banner .contents .title h3{
    font-size: 30px;
    font-weight: bold;
    color: #fff;
    margin: 20px 0 50px 0;
}
.inputWrap{
    display: flex;
    position: relative;

}
.inputWrap input{
    width: 100%;
    height: 45px;
    text-indent: 20px;
    border-radius: 99999px;
    border: none;
}
.inputWrap button{
    width: 100px;
    height: 45px;
    border-radius: 99999px;
    color: #fff;
    background-image: linear-gradient(120deg, #89f7fe 0%, #66a6ff 100%);
    font-weight: bold;
    position: absolute;
    right: 0;
    top: 0;
}