.modalPopup{
    color: #fff;
}
.modalPopup .modal_wrap{
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    left: 0;
    top: 0;
    background: rgba(0, 0, 0, 0.8);
    z-index: 1200;
}
.modalPopup .modal_wrap .modal{
    position: absolute;
    max-width: 920px;
    height: auto;
    z-index: 1200;
    background: #141414c9;
    padding: 10px 10px 0 10px;
    border-radius: 8px;
}
.modalPopup .modal_wrap .modal .imgbox{
    width: 100%;
}
.modalPopup .modal_wrap .modal .imgbox>img{
    width: 100%;
}
.modalPopup .modal_wrap .modal .imgbox>span{
    text-align: center;
    line-height: 30px;
    width: 30px;
    height: 30px;
    position: absolute;
    right: 20px;
    top: 20px;
    cursor: pointer;
    color: #fff;
    background: rgba(0, 0, 0, 0.6);
    border-radius: 100%;
}

.modalPopup .modal_wrap .modal .title{
    padding: 10px;
    /* position: relative;
    left: 30px;
    bottom: 150px; */
}
.modalPopup .modal_wrap .modal .title h1{
    font-size: 32px;
    margin-bottom: 20px;
    text-shadow: 5px 5px 5px #333;
}
.modalPopup .modal_wrap .modal .title .icon{
    display: flex;
    gap: 10px;
    
}
.modalPopup .modal_wrap .modal .title button.play{
    width: 150px;
    height: 50px;
    border-radius: 8px;
    background: #fff;
    color: #000;
    font-size: 20px;
    font-weight: bold;
}
.modalPopup .modal_wrap .modal .title button{
    width: 50px;
    height: 50px;
    color: #fff;
    background: rgba(0, 0, 0, 0.8);
    padding: 10px;
    border-radius: 100%;
    border: 1px solid #fff;
}

.modalPopup .modal_wrap .modal .content{
    padding: 10px;
    /* position: relative;
    left: 10px;
    bottom: 150px; */
}
.modalPopup .modal_wrap .modal .content p{
    margin-top: 10px;
}
.modalPopup .modal_wrap .modal .content p>span{
    color: #5dec7a;
}