.header{
    width: 100%;
    background: #032541;
}
.header .inner{
    width: 1400px;
    height: 70px;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
    justify-content: space-between;
}
.header .inner .hiwrap{
    display: flex;
    gap: 25px;
}
.header .inner .hiwrap .imgbox{
    cursor: pointer;
}
.header .inner .hiwrap .imgbox img{
    width: 150px;
}
.header .inner .hiwrap>ul{
    display: flex;
    gap: 25px;
    align-items: center;
}
.header .inner ul>li{
    position: relative;
}
.header .inner ul>li>a{
    color: #fff;
    font-weight: bold;

}
.header .inner ul>.depth2{
    padding: 15px 30px;
    background: #fff;
    position: absolute;
    left: 0;
    top: 40px;
    display: none;
}
.header .inner ul>.depth2>li>a{
    color: #333;
}
.header .inner>ul{
    display: flex;
    gap: 25px;
    align-items: center;
}
.header .inner>ul .ko>a{
    display: block;
    padding: 2px 5px;
    border: 1px solid #fff;
    border-radius: 4px;
}

.header_black{
    display: none;
    transition: all 0.5s ease-in-out;
}