.footer{
    width: 100%;
    background: #032541;
    padding: 45px 0 95px 0;
    display: flex;
    gap: 40px;
    justify-content: center;
    align-items: center;
    margin-top: 40px;
}
.footer .community .imgbox{
    width: 130px;
    margin-bottom: 35px;
}
.footer .community button{
    padding: 10px 20px;
    color: #1CAFD4;
    background: #fff;
    border-radius: 4px;
    font-weight: bold;
}
.footer .depth1{
    line-height: 24px;
}
.footer .depth1>li>a{
    font-size: 18px;
    font-weight: bold;
    color: #fff;
}
.footer .depth1 .depth2>li>a{
    font-size: 15px;
    color: #fff;
}
.depthWrap{
    display: flex;
    gap: 40px;
}